<script>
import _ from 'lodash';

export default {
  provide() {
    return {
      reload_captcha: this.reload_Captcha,
    };
  },
  data() {
    return {
      isRouterAlive: true,
      //   ÁôªÂÖ•Ë≥áÊñô(ÁÑ°È†àgoogleÈ©óË≠âÁ¢º)
      addForm_Data: {
        phone: '',
        password: '',
        captcha: '',
        googleVerifyCode: null,
      },
      // logo src
      logoSrc: '',
      // googleÈ©óË≠âÁ¢º
      googleForm_data: {
        phone: '',
      },
      // ÊòØÂê¶ÊúâÈúÄË¶ÅËº∏ÂÖ•googleÈ©óË≠âÁ¢º
      isGoogleLogin: null,
      // ÁôªÂÖ•Ë°®ÂñÆË¶èÂâá
      rules: {
        phone: [
          //   {
          //     type: 'number',
          //     validator: checkBet_value, // È©óË≠âË≥™ÊäºÈáëÈ°çÁØÑÂúç
          //     trigger: 'blur',
          //     // message: 'ÂßîË®óÈáëÈ°çÈúÄÂ§ßÊñº0,‰∏çÂèØË∂ÖÈÅéÂèØÁî®È§òÈ°ç',
          //   },
          {
            required: true,
            message: this.$t('plsTypeAccount'),
            trigger: 'blur',
          },
        ],

        password: [
          {
            required: true,
            message: this.$t('plsTypePwd'),
            trigger: 'blur',
          },
        ],

