          {
            required: true,
            message: this.$t('plsTypeCaptcha'),
            trigger: 'blur',
          },
        ],
      },
      // ÈåØË™§Ë®äÊÅØ
      wrongMsg: {
        phone: '',
        password: '',
        captcha: '',
        googleVerifyCode: '',
      },
    };
  },
  methods: {
    getSiteInfo() {
      this.$http.get('/open/siteInfo').then((res) => {
        console.log(res.data.data);
        this.logoSrc = res.data.data.logo;
      });
    },
    checkGoogleLogin() {
      this.$http.post('/open/checkIsGoogleLogin', this.googleForm_data).then((res) => {
        console.log(res.data.data);
        this.isGoogleLogin = res.data.data.isGoogleLogin;
      });
    },
    login() {
      this.googleForm_data.phone = this.addForm_Data.phone;
      this.checkGoogleLogin();
      this.$http.post('/open/login', this.addForm_Data).then((res) => {
        const loginMsg = res.data.msg;
        console.log(this.isGoogleLogin, 'È©óË≠âÁ¢ºÊòØÂê¶ÂïüÂãï');
        // this.checkGoogleLogin();
        if (this.isGoogleLogin === false) {
          if (res.data.code === 200) {
            console.log(res.data.msg);
            this.$swal.fire(`${this.$t('loginSuccess')}!`, this.$t('loginSuccess'), 'success');
            // localStorage.setItem('user', this.addForm_Data);
            this.$router.push('/');
          } else if (res.data.code === 422) {
            this.wrongMsg.phone = _.findKey(loginMsg, ['param', 'phone']);
            console.log(this.wrongMsg.phone);
            if (this.wrongMsg.phone !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.phone.msg}`, 'error');
            }
            this.wrongMsg.password = _.findKey(loginMsg, ['param', 'password']);
            console.log(this.wrongMsg.password);
            if (this.wrongMsg.password !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.password.msg}`, 'error');
            }
            this.wrongMsg.captcha = _.findKey(loginMsg, ['param', 'captcha']);
            console.log(this.wrongMsg.captcha);
            if (this.wrongMsg.captcha !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.captcha.msg}`, 'error');
            } else if (res.data.code === 500) {
              console.log(res.data.msg);
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg}`, 'error');
            }
            this.reload_Captcha();
          } else {
            console.log(res.data.msg);
            this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg}`, 'error');
            this.reload_Captcha();
          }
        }
        if (this.isGoogleLogin === null) {
          if (res.data.code === 200) {
            console.log(res.data.msg);
            this.$swal.fire(`${this.$t('loginSuccess')}!`, this.$t('loginSuccess'), 'success');
            // localStorage.setItem('user', this.addForm_Data);
            this.$router.push('/');
          } else if (res.data.code === 422) {
            this.wrongMsg.phone = _.findKey(loginMsg, ['param', 'phone']);
            console.log(this.wrongMsg.phone);
            if (this.wrongMsg.phone !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.phone.msg}`, 'error');
            }
            this.wrongMsg.password = _.findKey(loginMsg, ['param', 'password']);
            console.log(this.wrongMsg.password);
            if (this.wrongMsg.password !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.password.msg}`, 'error');
            }
            this.wrongMsg.captcha = _.findKey(loginMsg, ['param', 'captcha']);
            console.log(this.wrongMsg.captcha);
            if (this.wrongMsg.captcha !== undefined) {
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg.captcha.msg}`, 'error');
            } else if (res.data.code === 500) {
              console.log(res.data.msg);
              this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg}`, 'error');
            }
            this.reload_Captcha();
          } else {
            console.log(res.data.msg);
            this.$swal.fire(`${this.$t('loginFail')}!`, `${res.data.msg}`, 'error');
            this.reload_Captcha();
          }
        } else {
          console.log(this.isGoogleLogin, 'ÂïüÂãï');
          this.isGoogleLogin = true;
        }

        // this.reload_Captcha();
      });

      // this.isGoogleLogin = true;
      // console.log('ÈúÄË¶ÅgoogleÈ©óË≠âÁ¢ºÁôªÂÖ•');
    },
    // Âê´ÊúâgoogleÈ©óË≠âÁôªÂÖ•
    login_withGoogle() {
      this.$http.post('/open/login', this.addForm_Data).then((res) => {
        const loginMsg = res.data.msg;
        // this.checkGoogleLogin();
        if (res.data.code === 200) {
          console.log(res.data.msg);
          this.$swal.fire('ÁôªÂÖ•ÊàêÂäü!', 'ÁôªÂÖ•ÊàêÂäü', 'success');
          // localStorage.setItem('user', this.addForm_Data);
          this.$router.push('/');
        } else if (res.data.code === 422) {
          this.wrongMsg.phone = _.findKey(loginMsg, ['param', 'phone']);
          console.log(this.wrongMsg.phone);
          if (this.wrongMsg.phone !== undefined) {
            this.$swal.fire('ÁôªÂÖ•Â§±Êïó!', `${res.data.msg.phone.msg}`, 'error');
          }
          this.wrongMsg.password = _.findKey(loginMsg, ['param', 'password']);
          console.log(this.wrongMsg.password);
          if (this.wrongMsg.password !== undefined) {
            this.$swal.fire('ÁôªÂÖ•Â§±Êïó!', `${res.data.msg.password.msg}`, 'error');
          }
          this.wrongMsg.captcha = _.findKey(loginMsg, ['param', 'captcha']);
          console.log(this.wrongMsg.captcha);
          if (this.wrongMsg.captcha !== undefined) {
            this.$swal.fire('ÁôªÂÖ•Â§±Êïó!', `${res.data.msg.captcha.msg}`, 'error');
          }
          this.wrongMsg.googleVerifyCode = _.findKey(loginMsg, ['param', 'googleVerifyCode']);
          console.log(this.wrongMsg.googleVerifyCode);
          if (this.wrongMsg.googleVerifyCode !== undefined) {
            this.$swal.fire('ÁôªÂÖ•Â§±Êïó!', `${res.data.msg.googleVerifyCode.msg}`, 'error');
          }
          this.reload_Captcha();
        } else {
          console.log(res.data.msg);
          this.$swal.fire('ÁôªÂÖ•Â§±Êïó!', `${res.data.msg}`, 'error');
          this.reload_Captcha();
        }
      });
    },
    reflesh_captcha() {
      this.$http.get(`/captcha.svg?=${new Date().getTime()}`).then((res) => {
        console.log(res.img);
      });
    },
    // ÈáçÊñ∞Êï¥ÁêÜ
    reload_Captcha() {
      this.isRouterAlive = false;
      this.$nextTick(() => {
        // Âà∑Êñ∞ÂæåÂü∑Ë°å
        this.isRouterAlive = true;
        this.reflesh_captcha();
      });
    },
  },
  created() {
    this.reflesh_captcha();
    this.getSiteInfo();
  },
};
</script>

